import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/components/pages/Home'
import RegisterPage from '@/components/pages/Register'
import ListPage from '@/components/pages/List'
import ChartPage from '@/components/pages/Chart'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: HomePage
    },
    {
      path: '/register',
      name: 'Register',
      component: RegisterPage
    },
    {
      path: '/list',
      name: 'List',
      component: ListPage
    },
    {
      path: '/charts',
      name: 'Chart',
      component: ChartPage
    }
  ]
})



