<template>
  <v-slide-x-reverse-transition mode="out-in">
    <v-toolbar dark class="black">
      <v-btn icon :to="{name: 'Home'}">
        <v-icon>arrow_back</v-icon>
      </v-btn>
      <v-spacer></v-spacer>
      <v-toolbar-title class="text-xs-center">Registros</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-slide-x-reverse-transition>
        <v-text-field v-show="showSearch" v-model="request.search" label="Buscar registro"></v-text-field>
      </v-slide-x-reverse-transition>
      <v-btn icon class="hidden-xs-only" @click.native="showSearch = !showSearch">
        <v-icon>search</v-icon>
      </v-btn>
    </v-toolbar>

    <!--Lista de Registros-->
    <v-layout row wrap>
      <v-flex xs12>
      </v-flex>
    </v-layout>
  </v-slide-x-reverse-transition>
</template>

<script>
  import {VTextField} from 'vuetify'
  export default {
    data () {
      return {
        showSearch: false,
        request: {
          search: null
        }
      }
    },
    components: {
      VTextField
    }
  }
</script>


