<template>
  <v-app>
    <v-content>
      <div class="clock-block">
        <v-clock :blink="true"></v-clock>
      </div>
      <router-view/>
    </v-content>
    <v-footer :fixed="fixed" app>
      <span>Sistema de registro de compresores ROY C.A &copy; 2018 </span>
    </v-footer>
  </v-app>
</template>

<script>
  import VClock from 'vue-digital-clock'
  export default {
    data () {
      return {
        clipped: false,
        drawer: true,
        fixed: false,
        items: [{
          icon: 'bubble_chart',
          title: 'Inspire'
        }],
        miniVariant: false,
        right: true,
        rightDrawer: false,
        title: 'Vuetify.js'
      }
    },
    name: 'App',
    components: {
      VClock
    }
  }
</script>



