<template>
  <v-slide-x-reverse-transition mode="out-in">
    <v-form v-model="valid" @submit.prevent="add">
      <v-toolbar dark class="black">
        <v-btn icon :to="{name: 'Home'}">
          <v-icon>arrow_back</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
        <v-toolbar-title class="text-xs-center">Recepción de compresor</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>

      <!--Recolección de datos-->
      <v-layout row wrap class="pa-3">
        <v-flex xs5>
          <h3 class="text-xs-center"><v-icon color="black">person_add</v-icon> Datos del responsable</h3>
          <v-text-field
                  required
                  :rules="IdentificationRules"
                  label="Cédula de identidad"
                  v-model="request.customer.indentification"
                  placeholder="Ej: 18055214"
          >
          </v-text-field>
          <v-text-field
                  required
                  :rules="FullNameRules"
                  label="Nombre completo"
                  v-model="request.customer.full_name"
                  placeholder="Ej: Jonh Doe"
          >
          </v-text-field>
          <v-text-field
                  required
                  :rules="PhoneRules"
                  label="Nº Teléfono"
                  v-model="request.customer.phone"
                  placeholder="Ej: 04145555555"
          >
          </v-text-field>
          <v-text-field
                  required
                  :rules="EmailRules"
                  label="Correo electrónico"
                  v-model="request.customer.email"
                  placeholder="Ej: jonhdot@gmail.com"
          >
          </v-text-field>
          <h3 class="text-xs-center"><v-icon color="black">business</v-icon> Datos de la empresa</h3>
          <v-text-field
                  required
                  :rules="RequiredRules('El nombre de la compañia es obligatorio')"
                  label="Nombre"
                  v-model="request.company.name"
                  placeholder="Ej: Epa C.A"
          ></v-text-field>
          <v-text-field
                  required
                  label="R.I.F"
                  v-model="request.company.rif"
                  placeholder="Ej: J1234567892"
          ></v-text-field>
        </v-flex>
        <v-flex xs7>
          <h3 class="text-xs-center"><v-icon color="black">settings</v-icon> Datos de la pieza</h3>
          <v-radio-group label="Tipo de producto" v-model="request.productType" required row>
            <v-radio
                    v-for="(type, index) in productTypes"
                    :key="index"
                    :label="type.label"
                    :value="type.value"
                    color="primary"
            ></v-radio>
          </v-radio-group>
        </v-flex>
      </v-layout>

      <!--Guardar-->
      <v-btn
        fab
        large
        fixed
        bottom
        right
        :loading="loading"
        :disabled="!valid || loading"
        type="submit"
      >
        <v-icon>save</v-icon>
      </v-btn>
    </v-form>
  </v-slide-x-reverse-transition>
</template>

<script>
  import {RequiredRules, EmailRules, IdentificationRules, FullNameRules, PhoneRules} from '../../utils/validations'
  import {VForm, VTextField} from 'vuetify'
  import {VRadioGroup, VRadio} from 'vuetify/es5/components/VRadioGroup'
  export default {
    data () {
      return {
        RequiredRules,
        EmailRules,
        IdentificationRules,
        FullNameRules,
        PhoneRules,
        loading: false,
        valid: false,
        productTypes: [
          {label: 'Compresor', value: 'c'},
          {label: 'Valvula', value: 'v'},
          {label: 'Serro', value: 's'}
        ],
        request: {
          productType: null,
          customer: {
            indentification: null,
            full_name: null,
            phone: null,
            email: null
          },
          company: {
            name: null,
            rif: null
          }
        }
      }
    },
    methods: {
      add () {
        console.log('guardar compresor')
      }
    },
    components: {
      VForm,
      VTextField,
      VRadioGroup,
      VRadio
    }
  }
</script>


