<template>
  <v-container fluid>
    <v-slide-x-reverse-transition mode="out-in">
      <section class="home-page">
        <div class="text-xs-center">
          <h1>Sistema de control de compresores</h1>
          <img src="~@/assets/images/logo.png" alt="logo" width="300px">
        </div>

        <div class="menu">
          <v-layout row wrap>
            <v-flex xs12 md6>
              <v-btn fab large :to="{name: 'Register'}" dark class="black">
                <v-icon>note_add</v-icon>
              </v-btn>
            </v-flex>
            <v-flex xs12 md6>
              <v-btn fab large :to="{name: 'List'}" dark class="black">
                <v-icon>find_in_page</v-icon>
              </v-btn>
            </v-flex>
            <v-flex xs12 md12>
              <v-btn fab :to="{name: 'Chart'}" dark class="black">
                <v-icon>show_chart</v-icon>
              </v-btn>
            </v-flex>
          </v-layout>
        </div>
      </section>
    </v-slide-x-reverse-transition>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {}
    }
  }
</script>

<style scoped lang="stylus">
  .btn--floating
    width: 75px
    height: 75px
    .icon
      font-size 35px
    &.btn--large
      width 150px
      height  150px
      .icon
        font-size 60px
  .menu
    width 50%
    position: absolute
    left: 50%
    top: 50%
    -webkit-transform: translate(-50%, -50%)
    transform: translate(-50%, -50%)
    text-align center
</style>



